<template>
	<div class="showing-up">
		<ul @touchmove="moveflag=true">
			<li class="item" v-for="item in movieList" :key="item.filmId" @touchend="handleEnterMovieDetail(item.filmId)">
				<div class="item-img">
					<img :src="item.poster">
				</div>
				<div class="item-info">
					<h3 class="title">{{item.name}}</h3>
					<p class="grade">观众评分:
						<span style="color: #ffb539;">{{item.grade}}</span>
					</p>
					<p class="actor">主演:
						<span v-for="(val, i) in item.actors" :key="i">
							{{val.name}}
						</span>
					</p>
					<p class="run-time">{{item.nation}} | {{item.runtime}}分钟</p>
				</div>
			</li>
		</ul>
	</div>
</template>

<script>
export default {
	name: "showingup",
	data() {
		return {
			movieList: [],
			moveflag: false
		}
	},
	activated() {
		this.getShowingUpMovieList()
	},
	methods: {
		// 获取影片
		getShowingUpMovieList() {
			this.axios({
				url: "https://m.maizuo.com/gateway?cityId=310100&pageNum=1&pageSize=10&type=1&k=8859248",
				headers: {
					"X-Client-Info": '{"a":"3000","ch":"1002","v":"5.0.4","e":"1583716737571230650801","bc":"310100"}',
					"X-Host": "mall.film-ticket.film.list"
				}
			}).then(res => {
				// console.log(res.data)
				if(res.data.msg === "ok") {
                    this.movieList = res.data.data.films
				}
			})
		},
		handleEnterMovieDetail(movieId) {
			if(this.moveflag) {
				this.moveflag = false
				return;
			}
			// console.log(this.moveflag)
			this.$router.push("filmDetail/showing/" + movieId)
		}
	}
}	
</script>

<style scoped>
.showing-up {
	background-color: #d2e2ca;
}
.showing-up ul {
	padding-left: 15px;
	border-top: 1px solid #679ce2;
}
.showing-up ul li {
	position: relative;
	display:flex;
	flex-direction:row;
	justify-content:flex-start;
	align-items:center;
	width: 100%;
	height: 94px;
	padding: 15px 15px 15px 0;
	border-top: 1px solid red;
}
.showing-up ul li:first-of-type {
	border-top: 0;
}
.showing-up .item-img{
	position: absolute;
	display: flex;
	align-items: center;
	width: 66px;
	height: 94px;
	background: rgb(210, 226, 202);
}
.showing-up .item-img>img {
	width: 100%;
}
/* calc(100% - 10px) 函数用于动态计算长度值  + - * / */
.showing-up .item-info{
	position: absolute;
	left: 60px;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: flex-start;
	width: calc(100% - 120px);
	padding: 0 20px;
}
.showing-up .item-info .title,
.showing-up .item-info .grade,
.showing-up .item-info .actor,
.showing-up .item-info .run-time {
	width: 100%;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
	font-size: 14px;
	color: #686a6c;
}
.showing-up .item-info .title{
	/* margin-bottom: 10px; */
	font-size: 18px;
	color: #191a1b;
}
</style>